    import { Container } from '@material-ui/core';
    import { useState } from 'react';
    import {BrowserRouter, HashRouter, Route, Switch} from 'react-router-dom';
    import './App.css';
    import { Main } from './components/main/main';
    import Nav from './components/nav/nav';
    import Project from './components/project/project';
    import Work from './components/work/work';
    import NotFound from "./components/notFound/notFound";
    import Contact from "./components/contact/contact";



    function App() {

        const [lang, setLang] = useState("en");
        return (
        <Container className="wrap" >
        <HashRouter basename={process.env.PUBLIC_URL}>

        <div className="App">
        <Nav lang={lang} setLang={setLang}/>
        <Switch>
       <Route path="/" render={()=><Main lang={lang}/>} exact />
            <Route path="/annavaleska" render={()=><Main lang={lang}/>} exact />
       <Route path="/main" render={()=><Main lang={lang}/>}  />
       <Route path="/work" render={()=><Work lang={lang}/>} />
       <Route path="/project" render={()=><Project lang={lang} />} />
       <Route path="/contact" render={()=><Contact lang={lang}/>}/>
       <Route children={()=><NotFound lang={lang}/>} />
       </Switch>
        </div>

        </HashRouter>

        </Container>
      );
    }

    export default App;
